.mw-selfie__title {
  position: relative;
}

.mw-selfie__heading {
  position: absolute;
  top: 50%;
  left: 0;
  font-size: 36px;
  transform: translateY(-50%);
  width: 54%;
  padding: 56px 72px;
}

.mw-selfie__content {
  padding: 24px 72px;
}

.mw-selfie__image {
  max-width: 100%;
  object-fit: cover;
}
