.pros {
  margin-bottom: 100px;
}

.pros__wrapper {
  max-width: 1320px;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;
}

.pros__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: flex-start;
  list-style: none;
  padding: 0;
  margin: 0;
}

.pros__item {
  display: flex;
  margin-right: 10px;
  margin-left: 10px;
}

.pros__image {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 60px;
  min-height: 54px;
  margin-right: 10px;
}

.pros__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.pros__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  color: @text-color_title;
  margin-top: 0;
  margin-bottom: 4px;
}

.pros__description {
  font-size: 14px;
  font-weight: 400;
  color: @text-color_description;
  margin: 0;
}

@media screen and (max-width: 768px) {
  .pros__item {
    width: 240px;

    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }
}

@media screen and (max-width: 471px) {
  .pros__list {
    justify-content: center;
  }
}
