.stocks {
  transform: translateY(-100px);
}

.stocks__wrapper {
  max-width: 1320px;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;
}

.stocks__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.stocks__item {
  overflow: hidden;
  flex: auto;
  min-width: 100px;
  min-height: 200px;
  width: 200px;
  margin-right: 20px;
  margin-bottom: 20px;
  margin-left: 20px;
  border-radius: 10px;

  &:nth-child(1) {
    .stocks__button {
      background-image: url("../img/stocks-1.png");
      background-repeat: no-repeat;
      background-position: right bottom;
    }
  }

  &:nth-child(2) {
    .stocks__button {
      background-image: url("../img/stocks-2.png");
      background-repeat: no-repeat;
      background-position: left bottom;
    }
  }

  &:nth-child(3) {
    .stocks__button {
      background-image: url("../img/stocks-3.png");
      background-repeat: no-repeat;
      background-position: right bottom;
    }
  }

  &:nth-child(4) {
    .stocks__button {
      background-image: url("../img/stocks-4.png");
      background-repeat: no-repeat;
      background-position: right bottom;
    }
  }

  &:nth-child(5) {
    .stocks__button {
      background-image: url("../img/stocks-5.png");
      background-repeat: no-repeat;
      background-position: right -7px bottom -137px;
    }
  }
}

.stocks__button {
  display: inline-flex;
  padding: 28px 30px;
  height: 100%;
  width: 100%;
  border: none;
  text-align: left;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  background-color: @bg-color_stocks-light;

  &_bg-dark {
    background-color: @bg-color_stocks-dark;
  }
}

.stocks__description {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 16px;
  font-weight: 400;
  color: @text-color_stocks;

  &_lead {
    font-size: 18px;
    font-weight: 600;
  }

  &:not(:last-child) {
    margin-bottom: 30px;
  }
}

@media screen and (min-width: 992px) {
  .stocks__item {
    transform: scale(1);
    transition: transform 0.1s ease;
    cursor: pointer;

    &:hover,
    &:focus {
      transform: scale(1.03);
      transition: transform 0.1s ease;
    }

    &:active {
      transform: scale(0.99);
      transition: transform 0.1s ease;
    }
  }
}

@media screen and (max-width: 1240px) {
  .stocks__wrapper {
    max-width: 998px;
  }
}

@media screen and (max-width: 519px) {
  .stocks {
    transform: translateY(-100px);
  }

  .stocks__wrapper {
    max-width: 998px;
  }

  .stocks__item {
    min-height: 150px;
  }
}
