.page-header {
  position: fixed;
  background-color: transparent;
  width: 100vw;
  transform: translateY(56px);
  transition: all 0.25s cubic-bezier(0.0, 0.0, 0.2, 1);
  z-index: 100;
}

.page-header__page-nav {
  background-color: @bg-color_header;
  border-radius: 30px;
  box-shadow: @shadow_header;
}

.page-header.fixed {
  top: 0;
  height: 110px;
  background-color: @bg-color_header;
  box-shadow: @shadow_header;
  position: fixed;
  transform: translateY(0);
  transition: all 0.2s cubic-bezier(0.0, 0.0, 0.2, 1);

  .page-header__page-nav {
    box-shadow: none;
  }
}

.page-header__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 1320px;
  margin: 0 auto;
  padding: 24px 20px;
  position: relative;
}

.page-header__toggle {
  display: none;
}

@media screen and (max-width: 1200px) {
  .page-header {
    transform: translateY(24px);
  }

  .page-header_scrollable {
    transform: translateY(0);
  }
}

@media screen and (max-width: 992px) {
  .page-header {
    transform: translateY(0);
    background-color: @bg-color_header;
    box-shadow: @shadow_header;
  }

  .page-header__page-nav {
    background-color: transparent;
    border-radius: 0;
    box-shadow: inset 0 13px 20px -20px rgba(0, 0, 0, .35);
    transition: transform 0.2s ease-in;
  }

  .page-header__toggle {
    display: inline-flex;
  }

  .page-header__wrapper {
    padding-top: 14px;
    padding-bottom: 14px;
  }

  .page-header__toggle.is-active ~ .page-header__page-nav {
    transform: translateX(0);
    transition: transform 0.2s ease-out;
  }

}

.page-header_scrollable {
  transform: translateY(0);
}


@media screen and (max-width: 768px) {
  .page-header__wrapper {
    padding: 8px 25px;
  }
}
