.advantages {
  margin-bottom: 120px;
}

.advantages__wrapper {
  max-width: 1320px;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;
}

.advantages__list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
}

.advantages__item {
  flex: initial;
  width: 320px;

  &:nth-child(1) {
    .item__header {
      padding-left: 118px;
    }
  }

  &:nth-child(2) {
    .item__header {
      padding-left: 121px;
    }
  }

  &:nth-child(3) {
    .item__header {
      padding-left: 111px;
    }
  }
}

@media screen and (max-width: 992px) {
  .advantages__list {
    flex-direction: column;
  }

  .advantages__wrapper {
    max-width: 600px;
  }

  .advantages__item {
    width: auto;
    padding: 0;

    &:not(:last-child) {
      margin-bottom: 75px;
    }
  }
}
