.order {
  background-color: @bg-color_order;
}

.order__wrapper {
  max-width: 1320px;
  margin: 0 auto;
  padding: 60px 20px 20px;
}

.order__lead {
  font-size: 30px;
  font-weight: 600;
  color: @text-color_order-lead;
  text-align: center;
  margin-top: 0;
  margin-bottom: 6px;
}

.order__text {
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  color: @text-color_order;
  margin-top: 0;
  margin-bottom: 80px;
}

.order__list {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin: 0;
  list-style: none;
  justify-content: center;
  counter-reset: order;
}

.order__item {
  display: flex;
  flex-direction: column;
  flex: initial;
  width: 345px;
  padding-right: 40px;
  padding-left: 40px;
  margin-bottom: 88px;

  a {
    text-decoration: none;
  }
}

.order__header {
  padding-left: 64px;
  position: relative;

  &::before {
    counter-increment: order;
    content: counter(order);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 53px;
    height: 53px;
    border-radius: 50% 0 50% 50%;
    font-size: 30px;
    font-weight: 700;
    line-height: 18px;
    color: @text-color_order-counter;
    background-color: @bg-color_order-counter;
    transition: transform 0.15s ease;
    transform: scale(1);
  }
}

.order__title {
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 11px;
  font-weight: 700;
  line-height: 18px;
  color: @text-color_order;
  text-transform: uppercase;
}

.order__name {
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;
  color: @text-color_order-name;
  margin-top: 0;
  margin-bottom: 4px;
}

.order__subtitle {
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  color: @text-color_order-subtitle;
  margin-top: 0;
  margin-bottom: 20px;
  white-space: nowrap;
}

.order__desc {
  margin: 0;
  font-size: 14px;
  font-weight: 300;
  line-height: 16px;
  color: @text-color_order-desc;
  padding-left: 30px;
}

@media screen and (min-width: 992px) {
  .order__item {
    cursor: pointer;

    &:hover,
    &:focus {
      .order__header::before {
        transform: scale(1.1);
        transition: transform 0.15s ease;
        box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.20);
      }
    }
  }
}

@media (max-width: 768px) {
  .order__item {
    flex: auto;
  }
}

@media (max-width: 519px) {
  .order__subtitle {
    white-space: normal;
  }

  .order__item {
    padding: 0 20px;
  }

  .order__desc {
    padding-left: 0;
  }
}

