.gallery__wrapper {
  max-width: 1320px;
  margin-right: auto;
  margin-left: auto;
  padding: 70px 10px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.gallery__item {
  border-radius: 10px;
  overflow: hidden;
  flex: initial;
  margin: 30px;
  transform: scale(1);
  cursor: pointer;

  &:hover,
  &focus {
    transform: scale(1.01);
    transition: all 0.1s ease;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.20);
  }

  img {
    max-width: 100%;
    object-fit: cover;
    object-position: center;
    display: block;
  }
}

@media screen and (max-width: 1200px) {
  .gallery__item {
    width: 180px;
    height: 180px;
    margin: 15px;
  }
}

@media screen and (max-width: 992px) {
  .gallery__item {
    width: 140px;
    height: 140px;
    margin: 10px;
  }
}

@media screen and (max-width: 519px) {
  .gallery__item {
    width: 100px;
    height: 100px;
    margin: 5px;
  }
}
