.item {
  padding-right: 20px;
  padding-left: 20px;
}

.item__header {
  position: relative;
  display: flex;
  padding-left: 110px;
  align-items: flex-end;
  min-height: 100px;
  margin-bottom: 35px;
}

.item__image {
  position: absolute;
  bottom: 0;
  left: 0;

  img {
    max-width: 100%;
    height: auto;
  }
}

.item__title {
  margin: 0;
  font-size: 24px;
  font-weight: 600;
  line-height: 24px;
  color: @text-color_title;
}

.item__description {
  margin: 0;
  padding-left: 35px;
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  color: @text-color_description;
  width: 100%;
}

@media screen and (max-width: 992px) {
  .item__header {
    padding-left: 120px;
  }

  .item__description {
    padding-left: 0;
  }

  .item__header {
    margin-bottom: 25px;
    min-height: 50px;
  }
}
