.logo {
  display: flex;
  align-items: center;
  flex:   none;
  height: 48px;

  img {
    display: inline-block;
    max-width:  100%;
    height:     100%;
    object-position: left center;
    object-fit: contain;
  }
}

@media screen and (max-width: 768px) {
  .logo {
    width: 180px;
    height: 32px;
  }
}
