.page-nav {
  display: flex;
  height: 60px;
  padding-right: 50px;
  padding-left: 50px;
  max-width: 1000px;
  flex: auto;
  margin-left: 32px;

  & > div {
    width: 100%;
  }
}

.page-nav__list {
  display: flex;
  flex: auto;
  justify-content: space-around;
  padding-left: 0;
  margin: 0;
  list-style: none;
  height: auto;
}

.page-nav__link {
  display: inline-flex;
  align-items: center;
  height: 100%;
  color: @text-color_header;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  text-decoration: none;
  white-space: nowrap;

  &_icon {
    position: relative;
    padding-left: 48px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      width: 30px;
      height: 30px;
      background-image: url("../img/icon-instagram.png");
      background-repeat: no-repeat;
      background-position: 0 0;
    }
  }
}

@media screen and (min-width: 993px) {
  .page-nav__link {
    &:hover,
    &:focus {
      color: @text-color_header_hover;
    }

    &:active {
      color: lighten(@text-color_header_hover, 10%);
    }
  }
}

@media screen and (max-width: 1200px) {
  .page-nav {
    padding-right: 30px;
    padding-left: 30px;
  }

  .page-nav__link {
    font-size: 16px;
    line-height: 16px;

    &_icon {
      padding-left: 32px;

      &::before {
        width: 24px;
        height: 24px;
        background-size: contain;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .page-nav {
    position: absolute;
    top: 82px;
    right: 0;
    margin-left: 0;
    width: 320px;
    height: auto;
    background-color: @bg-color_header;
    padding-top: 40px;
    padding-bottom: 40px;
    transform: translate(100%);
  }

  .page-nav__list {
    flex-direction: column;
    align-items: center;
  }

  .page-nav__link {
    padding-top: 24px;
    padding-bottom: 24px;
    font-size: 24px;
    line-height: 24px;

  }
}

@media screen and (max-width: 768px) {
  .page-nav {
    top: 70px;
  }
}
